* {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  background-color: #f9f9f9;
  font-family: Poppins, "sans-serif";
  font-size: 62.5%;
}

a {
  text-decoration: none;
}

a:link, a:visited {
  color: #f9f9f9;
}

button {
  cursor: pointer;
  font-family: Poppins, "sans-serif";
}

body {
  color: #020203;
  background-color: #f9f9f9;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

.container {
  max-width: 1248px;
  margin: 0 auto;
  padding: 0 4.8rem;
}

.company-title {
  font-family: higuenregular, "sans-serif";
}

.title {
  color: #262626;
}

.navbar {
  z-index: 100;
  width: 100%;
  padding: 20px 0;
  transition: all .4s;
  position: fixed;
  top: 0;
}

.navbar .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navbar.scrolled {
  background-color: #0f0f0f;
}

.navbar h1 a {
  font-size: 2.4rem;
  text-decoration: none;
}

.navbar nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar nav li {
  margin-left: 20px;
  display: inline-block;
}

.navbar nav a {
  font-size: 1.6rem;
  text-decoration: none;
}

.mais-recentes {
  background-color: #262626;
  padding: 50px 0;
}

.mais-recentes h2 {
  color: #f9f9f9;
  margin-bottom: 2.4rem;
  font-size: 3.2rem;
}

.cards {
  grid-gap: 2.5rem;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.cards .card {
  background-color: "transparent";
  color: #15171b;
  background-color: #f6f6f6;
  border-radius: 1.2rem;
  padding: 2rem;
  display: flex;
  box-shadow: 0 1px 4px #0000001a;
}

.cards .card .card-description {
  color: #262626;
}

.cards .card .card-image {
  object-fit: cover;
  object-position: center;
  border-radius: 6px;
  width: 16rem;
  height: 16rem;
}

.cards .card .card-content {
  flex-direction: column;
  max-height: 16rem;
  padding-left: 1rem;
  display: flex;
}

.cards .card .card-content .card-title {
  margin-bottom: 10px;
  padding-top: 1rem;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.2;
}

.cards .card .card-content .card-description-container {
  width: 100%;
  height: 100%;
}

.cards .card .card-content .card-description-container .card-description {
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 1.5;
  display: -webkit-box;
  overflow: hidden;
}

.cards .card .entertainment-badge {
  color: #fff;
  background-color: #134354;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1.4rem;
  font-weight: 700;
  display: inline-block;
}

.cards .card:hover {
  transform: scale3d(1.005, 1.005, 1.005);
}

footer {
  color: #f9f9f9;
  text-align: center;
  background-color: #0f0f0f;
  padding: 2.4rem 0;
}

footer .contato {
  grid-template-columns: repeat(3, 1fr);
  padding: 50px 0;
  display: grid;
}

footer .contato .contato-info {
  flex-direction: column;
  align-items: center;
  display: flex;
}

footer .contato .contato-info h3 {
  margin-bottom: 2.4rem;
  font-size: 2.4rem;
}

footer .contato .contato-info p {
  font-size: 1.2rem;
  line-height: 1.7;
}

footer .lyrics {
  text-align: center;
  width: 100%;
}

.divisor {
  border-bottom: 2px solid #020203;
  width: 50rem;
  margin-top: 8rem;
}

.article-author {
  width: 80rem;
}

.article-author .about-author {
  margin-bottom: 0;
  padding-top: 1rem;
  font-size: 1.4rem;
}

.header {
  color: #fff;
  background-image: url("croche-tecnologia-bg.f3c11eeb.jpg");
  background-position: center;
  background-size: cover;
  height: 90vh;
  position: relative;
}

.header .container {
  height: 100%;
}

.header:after {
  content: "";
  opacity: .6;
  z-index: 0;
  background-color: #020203;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.header .brand-calling {
  margin-bottom: 2.4rem;
  font-size: 1.4rem;
}

.header .header-content {
  z-index: 1;
  flex-direction: column;
  justify-content: center;
  gap: 4.8rem;
  height: 100%;
  padding-top: 7rem;
  display: flex;
  position: relative;
}

.header .header-content h2 {
  font-family: higuenregular, "sans-serif";
  font-size: 6.4rem;
}

.header .header-content .title-description {
  font-family: higuenregular, "sans-serif";
  font-size: 4.8rem;
  line-height: 1.5;
}

.header .header-content .author {
  font-size: 1.6rem;
}

.header .header-content .post-day {
  font-size: 1.6rem;
  font-weight: 500;
}

.header .header-content .brand-calling {
  font-size: 2rem;
}

.text-development {
  color: #262626;
  background-color: #f9f9f9;
  padding: 50px 0;
}

.text-development h3 {
  margin-bottom: 20px;
  font-size: 3.2rem;
  font-weight: 600;
}

.text-development p {
  margin-bottom: 25px;
  font-size: 1.6rem;
  line-height: 1.5;
}

.text-development .flex-text_img {
  justify-content: center;
  display: flex;
}

.text-development .flex-text_img .flex-image {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.text-development .flex-text_img img {
  width: 50rem;
}

.text-development .flex-text_img p {
  margin-bottom: 0;
}

.text-development .header-image {
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
  display: flex;
}

.text-development .header-image img {
  width: 90%;
  height: 100%;
}

.text-development .header-image span {
  margin-top: 1rem;
  font-size: 1.2rem;
}

.text-development + .text-development {
  padding-top: 0;
}

.text-development .flex-text_img + .flex-text_img {
  padding-top: 3.2rem;
}

.text-development .flex-text_img {
  gap: 3.2rem;
  display: flex;
}

.text-development .flex-text_img .flex-image {
  justify-content: center;
}

.text-development .flex-text_img .flex-image img {
  width: 30rem;
}

.text-development .flex-text_img .text-container {
  padding: 3.2rem 0;
}

.text-development .flex-text_img .text-container p + p {
  margin-top: 2.4rem;
}

.text-development .flex-text_img .text-container li {
  font-size: 1.6rem;
}

.text-development .flex-text_img .text-container li + li {
  margin-top: 1.2rem;
}
